@import url(https://fonts.googleapis.com/css2?family=PT+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css?family=Roboto:300);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Encode Sans Expanded', sans-serif;
}

body{

    background-color: #fff;
}

.container-fluid{
    background-color: #fff;
}

.card{
    width: 20rem;
    height: 30rem;
    margin-bottom: 2rem;
    align-items: center;
}

@media only screen and (max-width: 960px){
    .card {
        width: 15rem;
    }
}
@media only screen and (max-width: 460px) {
    .card{
        width: 20rem;
    }
}
@media only screen and (max-width: 360px) {
    .card{
        width: 15rem;
    }
}

.heading{
    color: black;
    font-size: 48px;
    font-weight: 600;
    align-items: center;
    display: flex;
    margin-bottom: 20px;
    margin-top: -60px;
}

.card:hover{
    box-shadow:  5px 10px 20px 1px rgba(0,0,0,0.6)!important;
    /* box-shadow: 10px 10px blue; */
}

.card-body{
    padding: 2rem 0!important;

}

.card-text{
    font-size: 0.9rem;
    padding: 0.4rem 1.9rem;
}

 .container-fluid{
    padding-top: 6rem;
    padding-bottom: 6rem;
    width: 100%;
 } 

.overflow{
     overflow: hidden;
 }

.card-img-top{
    transform: scale(1);
    transition: transform 0.5s ease;
}

.card-img-top:hover{
    cursor: pointer;
    transform: scale(1.2);
}
.affiliation{
   height: 400px;
   margin-bottom: 50px;
   background: #f0f0f0 ;
}

.AffName{
    margin-top: 50px;
    padding-top: 50px;
    margin-bottom: 60px;
    text-align: center;
    color: #101010;
    font-weight: 600;
}

.Image{
    height: 150px;
    width: 200px;
    border: 2px solid black;
    display: block;
    margin: 2px 1em 0 auto;
}

.slick-arrow{
    color: black;
}

.slick-prev{
    z-index: 100;
    left: 20px !important;
}

.slick-next{
    z-index: 100;
    right: 20px !important;
}


